<template>
	<view class="box">
		<!-- https://ext.dcloud.net.cn/plugin?id=6806 -->
		<v-sign :width="sinWidth" :height="sinHeight" @init="onSignInit"></v-sign>
		
		<view class="sticky-bottom flex-btns">
			<u-button type="primary" class="my-primary-button" text="保存图片" @tap="saveImage">
			</u-button>
			<u-button type="primary" :plain="true" class="my-primary-button" text="清空" @tap="clear">
			</u-button>
			<u-button type="primary" class="my-primary-button" text="保存临时图片路径" @tap="saveTempFilePath">
			</u-button>
		</view>
		


	</view>
</template>

<script>
	export default {
		data() {
			return {
				sinWidth: 0,
				sinHeight: 0
			};
		},
		onLoad(option) {

		},
		onReady() {
			uni.getSystemInfo({
				success: (res) => {
					this.sinWidth=res.screenWidth+"px"
					this.sinHeight=res.screenHeight+"px"
				}
			});
		},
		onShow() {

		},
		methods: {
			onSignInit(signCtx) {
				this.signCtx = signCtx
			},
			// 清空
			clear() {
				this.signCtx.clear()
			},
			// 撤回
			revoke() {
				this.signCtx.revoke()
			},
			// 保存为临时图片路径，h5返回 base64
			async saveTempFilePath() {
				const res = await this.signCtx.canvasToTempFilePath()
				console.log(res)
			},
			// 保存 png 图片
			saveImage() {
				this.signCtx.saveImage()
			}


		},
	}
</script>

<style lang="scss" scoped>
	.box {
		.flex-btns{
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}
</style>
